export default {
name: "",
components: {

},
props: {},
data () {
  return {
	  trailerList: [],
	  page: 1,
	  pageSize: 30,
	  keywords: "",
	  lowerOpen: true
    }
  },
  methods: {
	// 获取预告片列表
	getTrailerList() {
		uni.request({
			url: `${this.$api}/search/list?qq=122212489&keywords=${this.keywords}&page=${this.page}&pageSize=${this.pageSize}`,
			method: 'POST',
			data: {},
			success: res => {
				if(res.data.data.rows.length === 0) {
					this.lowerOpen = false
				}else {
					if(this.page > 0) {
						this.trailerList = this.trailerList.concat(res.data.data.rows)
					}else {
						this.trailerList = res.data.data.rows
					}
				}
				console.log(res,'预告片列表')
			},
			fail: () => {},
			complete: () => {}
		});
	},
	// 上拉刷新
	lower() {
		if(this.lowerOpen) {
			this.page += 1
			this.getTrailerList()
		}
	},
	// 获取关键字
	getKeyWords(e) {
		this.keywords = e.detail.value
		this.page = 0
		this.getTrailerList()
	},
	// 搜索
	search() {
		this.page = 0
		this.getTrailerList()
	},
	// 去详情页面
	goDetail(id) {
		uni.navigateTo({
			url: `/pages/detail/detail?id=${id}`,
			success: res => {},
			fail: () => {},
			complete: () => {}
		});
	}
  },
  mounted () {

  },
  onLoad () {
	this.getTrailerList();
  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
